import React from 'react';
import { BtnItem, BtnList, Link } from './MatrixBtn.styled';

const btnList = [
  { name: ' ', to: 'fateMatrix' },
  { name: ' ', to: 'karmaIssues' },
  { name: ' ', to: 'healthMatrix' },
  { name: '', to: 'prognosis' },
];

const MatrixBtn = () => {
  return (
    <BtnList>
      {btnList.map(({ name, to }) => (
        <BtnItem key={name}>
          <Link to={to}>{name}</Link>
        </BtnItem>
      ))}
    </BtnList>
  );
};

export default MatrixBtn;
